// Google Maps utilities
const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || 'AIzaSyCnxQw7S360KrYOWAtlk5OHukrJvxqXVlc';

export interface DistanceResult {
  distance: number; // in kilometers
  duration: string;
  status: 'OK' | 'ERROR';
  error?: string;
}

export async function calculateDistance(origin: string, destination: string): Promise<DistanceResult> {
  try {
    const response = await fetch('/api/maps/distance', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ origin, destination }),
    });

    const data = await response.json();
    return data;
  } catch (error) {
    return {
      distance: 0,
      duration: '',
      status: 'ERROR',
      error: 'Error de conexi√≥n al calcular la distancia'
    };
  }
}

export function calculateFare(distance: number): number {
  if (distance <= 1) {
    return 1.80;
  } else {
    return distance * 16;
  }
}

export function getMapEmbedUrl(origin: string, destination: string): string {
  const baseUrl = 'https://www.google.com/maps/embed/v1/directions';
  const params = new URLSearchParams({
    key: GOOGLE_MAPS_API_KEY,
    origin: origin,
    destination: destination,
    mode: 'driving'
  });
  
  return `${baseUrl}?${params.toString()}`;
}

// Utility function to validate addresses (basic validation)
export function isValidAddress(address: string): boolean {
  return address.trim().length >= 10; // Basic validation
}
