'use client';

import { useState, useEffect } from 'react';
import { db, TravelRequest, MessagingRequest } from '@/lib/database';
import { clearAdminSession } from '@/lib/auth';
import { calculateDistance, calculateFare, getMapEmbedUrl } from '@/lib/maps';
import GooglePlacesAutocomplete from '@/components/GooglePlacesAutocomplete';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from 'chart.js';
import { Bar, Doughnut } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
);

export default function AdminDashboard() {
  const [activeTab, setActiveTab] = useState<'dashboard' | 'travel' | 'messaging' | 'calculator' | 'config'>('dashboard');
  const [travelRequests, setTravelRequests] = useState<TravelRequest[]>([]);
  const [messagingRequests, setMessagingRequests] = useState<MessagingRequest[]>([]);
  const [filterStatus, setFilterStatus] = useState<string>('all');
  const [dateFilter, setDateFilter] = useState({
    from: '',
    to: '',
    current: 'today'
  });
  
  // Calculator state
  const [origin, setOrigin] = useState('');
  const [destination, setDestination] = useState('');
  const [distance, setDistance] = useState<number | null>(null);
  const [calculatedFare, setCalculatedFare] = useState<number | null>(null);
  const [finalFare, setFinalFare] = useState<number | null>(null);
  const [isCalculating, setIsCalculating] = useState(false);
  const [mapUrl, setMapUrl] = useState('');

  useEffect(() => {
    loadData();
  }, []);

  const loadData = () => {
    setTravelRequests(db.getTravelRequests());
    setMessagingRequests(db.getMessagingRequests());
  };

  const handleLogout = () => {
    clearAdminSession();
    window.location.reload();
  };

  const updateTravelStatus = (id: string, status: TravelRequest['estado']) => {
    db.updateTravelRequest(id, { estado: status });
    loadData();
  };

  const updateMessagingStatus = (id: string, status: MessagingRequest['estado']) => {
    db.updateMessagingRequest(id, { estado: status });
    loadData();
  };

  const handleCalculateDistance = async () => {
    if (!origin || !destination) {
      alert('Por favor, ingresa tanto el origen como el destino');
      return;
    }

    setIsCalculating(true);
    
    try {
      const result = await calculateDistance(origin, destination);
      
      if (result.status === 'OK') {
        setDistance(result.distance);
        const fare = calculateFare(result.distance);
        setCalculatedFare(fare);
        setFinalFare(fare);
        setMapUrl(getMapEmbedUrl(origin, destination));
      } else {
        alert(`Error: ${result.error}`);
      }
    } catch (error) {
      alert('Error al calcular la distancia');
    }
    
    setIsCalculating(false);
  };

  // Filter data based on date filter
  const getFilteredData = () => {
    const now = new Date();
    let startDate: Date;
    let endDate: Date = now;

    switch (dateFilter.current) {
      case 'today':
        startDate = new Date(now.getFullYear(), now.getMonth(), now.getDate());
        break;
      case 'week':
        startDate = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
        break;
      case 'month':
        startDate = new Date(now.getFullYear(), now.getMonth(), 1);
        break;
      case 'custom':
        startDate = dateFilter.from ? new Date(dateFilter.from) : new Date(0);
        endDate = dateFilter.to ? new Date(dateFilter.to) : now;
        break;
      default:
        startDate = new Date(0);
    }

    const filteredTravel = travelRequests.filter(req => {
      const reqDate = new Date(req.fecha_registro);
      return reqDate >= startDate && reqDate <= endDate;
    });

    const filteredMessaging = messagingRequests.filter(req => {
      const reqDate = new Date(req.fecha_registro);
      return reqDate >= startDate && reqDate <= endDate;
    });

    return { filteredTravel, filteredMessaging };
  };

  // Generate hourly data for charts
  const getHourlyData = () => {
    const { filteredTravel, filteredMessaging } = getFilteredData();
    const hourlyData = Array(24).fill(0);
    const hourlyMessaging = Array(24).fill(0);

    filteredTravel.forEach(req => {
      const hour = new Date(req.fecha_registro).getHours();
      hourlyData[hour]++;
    });

    filteredMessaging.forEach(req => {
      const hour = new Date(req.fecha_registro).getHours();
      hourlyMessaging[hour]++;
    });

    return { hourlyData, hourlyMessaging };
  };

  const { hourlyData, hourlyMessaging } = getHourlyData();
  const { filteredTravel, filteredMessaging } = getFilteredData();

  const filteredTravelRequests = filterStatus === 'all' 
    ? travelRequests 
    : travelRequests.filter(req => req.estado === filterStatus);

  const filteredMessagingRequests = filterStatus === 'all' 
    ? messagingRequests 
    : messagingRequests.filter(req => req.estado === filterStatus);

  // Chart data
  const hourlyChartData = {
    labels: Array.from({ length: 24 }, (_, i) => `${i}:00`),
    datasets: [
      {
        label: 'Solicitudes de Viaje',
        data: hourlyData,
        backgroundColor: 'rgba(76, 175, 80, 0.6)',
        borderColor: 'rgba(76, 175, 80, 1)',
        borderWidth: 1,
      },
      {
        label: 'Solicitudes de Mensajer√≠a',
        data: hourlyMessaging,
        backgroundColor: 'rgba(255, 193, 7, 0.6)',
        borderColor: 'rgba(255, 193, 7, 1)',
        borderWidth: 1,
      },
    ],
  };

  const statusChartData = {
    labels: ['Pendiente', 'Procesando', 'Completado', 'Cancelado'],
    datasets: [
      {
        data: [
          [...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'pendiente').length,
          [...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'procesando').length,
          [...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'completado').length,
          [...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'cancelado').length,
        ],
        backgroundColor: [
          'rgba(255, 193, 7, 0.8)',
          'rgba(33, 150, 243, 0.8)',
          'rgba(76, 175, 80, 0.8)',
          'rgba(244, 67, 54, 0.8)',
        ],
        borderWidth: 2,
      },
    ],
  };

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <h1 className="text-2xl font-bold text-gray-900 font-montserrat">
              Panel Administrativo - Go Dispatch M√©xico
            </h1>
            <button
              onClick={handleLogout}
              className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded-lg font-semibold"
            >
              Cerrar Sesi√≥n
            </button>
          </div>
        </div>
      </header>

      {/* Navigation Tabs */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <nav className="flex space-x-8">
            <button
              onClick={() => setActiveTab('dashboard')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'dashboard'
                  ? 'border-green-500 text-green-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              üìä Dashboard
            </button>
            <button
              onClick={() => setActiveTab('travel')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'travel'
                  ? 'border-green-500 text-green-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              üöñ Solicitudes de Viaje ({travelRequests.length})
            </button>
            <button
              onClick={() => setActiveTab('messaging')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'messaging'
                  ? 'border-green-500 text-green-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              üì¶ Solicitudes de Mensajer√≠a ({messagingRequests.length})
            </button>
            <button
              onClick={() => setActiveTab('calculator')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'calculator'
                  ? 'border-green-500 text-green-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              üßÆ Calculadora de Tarifas
            </button>
            <button
              onClick={() => setActiveTab('config')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'config'
                  ? 'border-green-500 text-green-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              ‚öôÔ∏è Configuraci√≥n
            </button>
          </nav>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Dashboard */}
        {activeTab === 'dashboard' && (
          <div className="space-y-6">
            {/* Date Filter */}
            <div className="bg-white p-6 rounded-lg shadow">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Filtros de Fecha</h2>
              <div className="grid md:grid-cols-4 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Per√≠odo
                  </label>
                  <select
                    value={dateFilter.current}
                    onChange={(e) => setDateFilter({ ...dateFilter, current: e.target.value })}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                  >
                    <option value="today">Hoy</option>
                    <option value="week">√öltima semana</option>
                    <option value="month">Este mes</option>
                    <option value="custom">Personalizado</option>
                  </select>
                </div>
                {dateFilter.current === 'custom' && (
                  <>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Desde
                      </label>
                      <input
                        type="date"
                        value={dateFilter.from}
                        onChange={(e) => setDateFilter({ ...dateFilter, from: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Hasta
                      </label>
                      <input
                        type="date"
                        value={dateFilter.to}
                        onChange={(e) => setDateFilter({ ...dateFilter, to: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                      />
                    </div>
                  </>
                )}
              </div>
            </div>

            {/* Stats Cards */}
            <div className="grid md:grid-cols-4 gap-6">
              <div className="bg-white p-6 rounded-lg shadow">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <span className="text-green-600 font-bold">üöñ</span>
                    </div>
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Total Viajes</p>
                    <p className="text-2xl font-bold text-gray-900">{filteredTravel.length}</p>
                  </div>
                </div>
              </div>

              <div className="bg-white p-6 rounded-lg shadow">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-yellow-100 rounded-full flex items-center justify-center">
                      <span className="text-yellow-600 font-bold">üì¶</span>
                    </div>
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Total Mensajer√≠a</p>
                    <p className="text-2xl font-bold text-gray-900">{filteredMessaging.length}</p>
                  </div>
                </div>
              </div>

              <div className="bg-white p-6 rounded-lg shadow">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                      <span className="text-blue-600 font-bold">‚è≥</span>
                    </div>
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Pendientes</p>
                    <p className="text-2xl font-bold text-gray-900">
                      {[...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'pendiente').length}
                    </p>
                  </div>
                </div>
              </div>

              <div className="bg-white p-6 rounded-lg shadow">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <span className="text-green-600 font-bold">‚úÖ</span>
                    </div>
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Completados</p>
                    <p className="text-2xl font-bold text-gray-900">
                      {[...filteredTravel, ...filteredMessaging].filter(req => req.estado === 'completado').length}
                    </p>
                  </div>
                </div>
              </div>
            </div>

            {/* Charts */}
            <div className="grid md:grid-cols-2 gap-6">
              <div className="bg-white p-6 rounded-lg shadow">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Solicitudes por Hora</h3>
                <Bar
                  data={hourlyChartData}
                  options={{
                    responsive: true,
                    plugins: {
                      legend: {
                        position: 'top' as const,
                      },
                      title: {
                        display: true,
                        text: 'Distribuci√≥n de Solicitudes por Hora del D√≠a',
                      },
                    },
                    scales: {
                      y: {
                        beginAtZero: true,
                      },
                    },
                  }}
                />
              </div>

              <div className="bg-white p-6 rounded-lg shadow">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Estado de Solicitudes</h3>
                <Doughnut
                  data={statusChartData}
                  options={{
                    responsive: true,
                    plugins: {
                      legend: {
                        position: 'bottom' as const,
                      },
                      title: {
                        display: true,
                        text: 'Distribuci√≥n por Estado',
                      },
                    },
                  }}
                />
              </div>
            </div>
          </div>
        )}

        {/* Filter for Travel and Messaging tabs */}
        {(activeTab === 'travel' || activeTab === 'messaging') && (
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Filtrar por estado:
            </label>
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
            >
              <option value="all">Todos</option>
              <option value="pendiente">Pendiente</option>
              <option value="procesando">Procesando</option>
              <option value="completado">Completado</option>
              <option value="cancelado">Cancelado</option>
            </select>
          </div>
        )}

        {/* Travel Requests */}
        {activeTab === 'travel' && (
          <div className="bg-white shadow rounded-lg overflow-hidden">
            <div className="px-6 py-4 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Solicitudes de Viaje</h2>
            </div>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Cliente
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Ruta
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Fecha
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Estado
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Acciones
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {filteredTravelRequests.map((request) => (
                    <tr key={request.id}>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {request.nombre_cliente}
                          </div>
                          <div className="text-sm text-gray-500">
                            {request.telefono}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <div className="text-sm text-gray-900">
                          <div><strong>Origen:</strong> {request.origen}</div>
                          <div><strong>Destino:</strong> {request.destino}</div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {request.fecha_salida ? new Date(request.fecha_salida).toLocaleString('es-MX') : 'No especificada'}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                          request.estado === 'pendiente' ? 'bg-yellow-100 text-yellow-800' :
                          request.estado === 'procesando' ? 'bg-blue-100 text-blue-800' :
                          request.estado === 'completado' ? 'bg-green-100 text-green-800' :
                          'bg-red-100 text-red-800'
                        }`}>
                          {request.estado}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <select
                          value={request.estado}
                          onChange={(e) => updateTravelStatus(request.id, e.target.value as TravelRequest['estado'])}
                          className="text-sm border border-gray-300 rounded px-2 py-1"
                        >
                          <option value="pendiente">Pendiente</option>
                          <option value="procesando">Procesando</option>
                          <option value="completado">Completado</option>
                          <option value="cancelado">Cancelado</option>
                        </select>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {/* Messaging Requests */}
        {activeTab === 'messaging' && (
          <div className="bg-white shadow rounded-lg overflow-hidden">
            <div className="px-6 py-4 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Solicitudes de Mensajer√≠a</h2>
            </div>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Remitente
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Destinatario
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Tipo
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Estado
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Acciones
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {filteredMessagingRequests.map((request) => (
                    <tr key={request.id}>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {request.remitente_nombre}
                          </div>
                          <div className="text-sm text-gray-500">
                            {request.remitente_telefono}
                          </div>
                          <div className="text-sm text-gray-500">
                            {request.direccion_recogida}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {request.destinatario_nombre}
                          </div>
                          <div className="text-sm text-gray-500">
                            {request.destinatario_telefono}
                          </div>
                          <div className="text-sm text-gray-500">
                            {request.direccion_entrega}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {request.tipo_paquete}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                          request.estado === 'pendiente' ? 'bg-yellow-100 text-yellow-800' :
                          request.estado === 'procesando' ? 'bg-blue-100 text-blue-800' :
                          request.estado === 'completado' ? 'bg-green-100 text-green-800' :
                          'bg-red-100 text-red-800'
                        }`}>
                          {request.estado}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <select
                          value={request.estado}
                          onChange={(e) => updateMessagingStatus(request.id, e.target.value as MessagingRequest['estado'])}
                          className="text-sm border border-gray-300 rounded px-2 py-1"
                        >
                          <option value="pendiente">Pendiente</option>
                          <option value="procesando">Procesando</option>
                          <option value="completado">Completado</option>
                          <option value="cancelado">Cancelado</option>
                        </select>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {/* Calculator */}
        {activeTab === 'calculator' && (
          <div className="bg-white shadow rounded-lg p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-6">Calculadora de Tarifas</h2>
            
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Direcci√≥n de origen
                  </label>
                  <GooglePlacesAutocomplete
                    value={origin}
                    onChange={setOrigin}
                    placeholder="Ingresa la direcci√≥n de origen"
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Direcci√≥n de destino
                  </label>
                  <GooglePlacesAutocomplete
                    value={destination}
                    onChange={setDestination}
                    placeholder="Ingresa la direcci√≥n de destino"
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                  />
                </div>

                <button
                  onClick={handleCalculateDistance}
                  disabled={isCalculating || !origin || !destination}
                  className="w-full bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-md disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isCalculating ? 'Calculando...' : 'Calcular Distancia y Tarifa'}
                </button>

                {distance !== null && (
                  <div className="bg-gray-50 p-4 rounded-md space-y-2">
                    <div>
                      <strong>Distancia estimada:</strong> {distance} km
                    </div>
                    <div>
                      <strong>Tarifa calculada:</strong> ${calculatedFare?.toFixed(2)} MXN
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        Tarifa final (editable):
                      </label>
                      <input
                        type="number"
                        step="0.01"
                        value={finalFare || ''}
                        onChange={(e) => setFinalFare(parseFloat(e.target.value))}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                      />
                    </div>
                  </div>
                )}
              </div>

              <div>
                {mapUrl && (
                  <div>
                    <h3 className="text-sm font-medium text-gray-700 mb-2">Ruta en el mapa:</h3>
                    <iframe
                      width="100%"
                      height="300"
                      frameBorder="0"
                      style={{ border: 0, marginTop: 12 }}
                      src={mapUrl}
                      allowFullScreen
                      className="rounded-md"
                    />
                  </div>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Configuration */}
        {activeTab === 'config' && (
          <div className="bg-white shadow rounded-lg p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-6">Configuraci√≥n</h2>
            
            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Logo del sistema
                </label>
                <input
                  type="file"
                  accept="image/*"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-green-500 focus:border-green-500"
                />
                <p className="text-sm text-gray-500 mt-1">
                  Sube el logo de tu empresa (formato PNG, JPG o SVG)
                </p>
              </div>

              <div className="bg-gray-50 p-4 rounded-md">
                <h3 className="font-medium text-gray-900 mb-2">API de Google Maps</h3>
                <p className="text-sm text-gray-600">
                  <strong>API Key configurada:</strong> {process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY?.substring(0, 20)}...
                </p>
                <p className="text-sm text-gray-500 mt-1">
                  La API key est√° configurada y lista para usar con Distance Matrix y Maps Embed.
                </p>
              </div>

              <div className="bg-blue-50 p-4 rounded-md">
                <h3 className="font-medium text-blue-900 mb-2">F√≥rmula de Tarifas</h3>
                <ul className="text-sm text-blue-800 space-y-1">
                  <li>‚Ä¢ Si distancia ‚â§ 1 km ‚Üí tarifa = $1.80 MXN</li>
                  <li>‚Ä¢ Si distancia {'>'} 1 km ‚Üí tarifa = distancia √ó $16 MXN</li>
                </ul>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
