// Database utilities for storing requests locally
export interface TravelRequest {
  id: string;
  nombre_cliente: string;
  telefono: string;
  origen: string;
  destino: string;
  fecha_salida?: string;
  observaciones?: string;
  distancia_km?: number;
  tarifa_calculada?: number;
  tarifa_final?: number;
  estado: 'pendiente' | 'procesando' | 'completado' | 'cancelado';
  fecha_registro: string;
}

export interface MessagingRequest {
  id: string;
  tipo_paquete: 'documento' | 'caja' | 'otro';
  remitente_nombre: string;
  remitente_telefono: string;
  direccion_recogida: string;
  destinatario_nombre: string;
  destinatario_telefono: string;
  direccion_entrega: string;
  detalles?: string;
  estado: 'pendiente' | 'procesando' | 'completado' | 'cancelado';
  fecha_registro: string;
}

class LocalDatabase {
  private getStorageKey(collection: string): string {
    return `transport_pwa_${collection}`;
  }

  // Travel requests methods
  saveTravelRequest(request: Omit<TravelRequest, 'id' | 'fecha_registro' | 'estado'>): TravelRequest {
    // Only run on client side
    if (typeof window === 'undefined') {
      throw new Error('Database operations only available on client side');
    }

    const newRequest: TravelRequest = {
      ...request,
      id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      fecha_registro: new Date().toISOString(),
      estado: 'pendiente'
    };

    const existing = this.getTravelRequests();
    existing.push(newRequest);
    localStorage.setItem(this.getStorageKey('viajes_solicitados'), JSON.stringify(existing));
    
    return newRequest;
  }

  getTravelRequests(): TravelRequest[] {
    // Only run on client side
    if (typeof window === 'undefined') {
      return [];
    }

    const data = localStorage.getItem(this.getStorageKey('viajes_solicitados'));
    return data ? JSON.parse(data) : [];
  }

  updateTravelRequest(id: string, updates: Partial<TravelRequest>): void {
    // Only run on client side
    if (typeof window === 'undefined') {
      return;
    }

    const requests = this.getTravelRequests();
    const index = requests.findIndex(r => r.id === id);
    if (index !== -1) {
      requests[index] = { ...requests[index], ...updates };
      localStorage.setItem(this.getStorageKey('viajes_solicitados'), JSON.stringify(requests));
    }
  }

  // Messaging requests methods
  saveMessagingRequest(request: Omit<MessagingRequest, 'id' | 'fecha_registro' | 'estado'>): MessagingRequest {
    // Only run on client side
    if (typeof window === 'undefined') {
      throw new Error('Database operations only available on client side');
    }

    const newRequest: MessagingRequest = {
      ...request,
      id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      fecha_registro: new Date().toISOString(),
      estado: 'pendiente'
    };

    const existing = this.getMessagingRequests();
    existing.push(newRequest);
    localStorage.setItem(this.getStorageKey('mensajeria_solicitada'), JSON.stringify(existing));
    
    return newRequest;
  }

  getMessagingRequests(): MessagingRequest[] {
    // Only run on client side
    if (typeof window === 'undefined') {
      return [];
    }

    const data = localStorage.getItem(this.getStorageKey('mensajeria_solicitada'));
    return data ? JSON.parse(data) : [];
  }

  updateMessagingRequest(id: string, updates: Partial<MessagingRequest>): void {
    // Only run on client side
    if (typeof window === 'undefined') {
      return;
    }

    const requests = this.getMessagingRequests();
    const index = requests.findIndex(r => r.id === id);
    if (index !== -1) {
      requests[index] = { ...requests[index], ...updates };
      localStorage.setItem(this.getStorageKey('mensajeria_solicitada'), JSON.stringify(requests));
    }
  }
}

export const db = new LocalDatabase();
